import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';

import { MaterialModule } from '../../material.module';

import { FlexLayoutModule } from '@angular/flex-layout';
import { SharedModule } from 'src/app/shared/shared.module';
import { MatInputModule } from '@angular/material';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { DeliveryChannelComponent } from './delivery-channel.component';

const routes: Routes = [
  {
    path: '',
    component: DeliveryChannelComponent
  },
  { path: 'update/channel/:id', component: DeliveryChannelComponent },
  {
    path: '',
    redirectTo: '',
    pathMatch: 'full'
  },
  // { path: '**', component: LoginComponent }
];


@NgModule({
  declarations: [DeliveryChannelComponent],
  imports: [
    CommonModule,
    MaterialModule,
    MatInputModule,
    SharedModule,
    FlexLayoutModule,
    FormsModule,
    ReactiveFormsModule,
    RouterModule.forChild(routes)
  ]
})
export class DeliveryChannelModule { }
