import { Component, OnInit } from '@angular/core';
import { RoutingService } from '../../shared/services/routing.service';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { CommonService } from '../../shared/services/common-service.service';
import { DeliveryChannelService } from 'src/app/shared/services/delivery-channel.service';

@Component({
  selector: 'haribol-delivery-channel',
  templateUrl: './delivery-channel.component.html',
  styleUrls: ['./delivery-channel.component.scss']
})
export class DeliveryChannelComponent implements OnInit {
  public deliveryForm: FormGroup;
  public deliveryChannels: any;
  public isListEnabled: any = false;
  constructor(
    public routingService: RoutingService,
    public fb: FormBuilder,
    public commonService: CommonService,
    public deliveryChannelService: DeliveryChannelService
  ) { }

  ngOnInit() {
    this.deliveryForm = this.fb.group({
      code: ['', Validators.required],
      name: ['', [Validators.required]],
      pinCode: ['', [Validators.required, Validators.pattern('^[1-9][0-9]{5}$')]]
    });
    this.init();
  }
  init() {
    this.deliveryChannelService.getDeliveryChannels().subscribe(res => {
      this.deliveryChannels = res;
    });
  }
  /** View Channels list */
  viewChannelList() {
    this.isListEnabled = !this.isListEnabled;
  }
  /**
   *redirect to product list page
   */
  redirectTo(product) {
    this.routingService.goToPage(product);

  }
  /**
   * Save delivery channel
   */
  saveDeliveryChannel(data) {
    this.commonService.validate(this.deliveryForm);
    if (this.deliveryForm.valid) {
      this.deliveryChannelService.postDeliveryChannels(this.deliveryForm.value).subscribe(res => {
        // this.deliveryChannels = res;
        this.commonService.showSnackBar('Delivery channel saved!!', 'Success');
        this.deliveryForm.reset();
        this.deliveryForm.updateValueAndValidity({ onlySelf: true });
        this.init();
      }, err => {
        this.commonService.showSnackBar('Something went wrong!!', 'Failure');
      });
    }

  }
  /**
   * Delivery channel remove
   */
  removeDeliveryChannel(channel) {
    this.deliveryChannelService.deleteDeliveryChannels({ channelId: channel.channelId }).subscribe(res => {
      this.commonService.showSnackBar('Successfully deleted the channel!!', 'Deleted');
    }, err => {
      this.commonService.showSnackBar('Something went wrong!!', 'Deleted');
    });
  }
  /**
   * Update delivery channel
   */
  updateDeliveryChannel(channel: any) {
    this.deliveryChannelService.updateDeliveryChannel(channel.channelId).subscribe(res => {
      this.commonService.showSnackBar('Successfully updated the channel!!', 'Updated');
    }, err => {
      this.commonService.showSnackBar('Something went wrong!!', 'updated');
    });
  }
  /**
   * Update delivery BOY
   */
  updateDeliveryBoy(deliveryboy: any) {
    this.deliveryChannelService.updateDeliveryBoy(deliveryboy).subscribe(res => {
      this.commonService.showSnackBar('Successfully updated deliveryBoy!!', ' Success');
    }, err => {
      this.commonService.showSnackBar('Something went wrong!!', 'Success');
    });
  }
  /**
   * Update delivery CUSTOMER
   */
  updateDeliveryCustomer(customer: any) {
    this.deliveryChannelService.updateDeliveryCustomer(customer).subscribe(res => {
      this.commonService.showSnackBar('Successfully updated the channel customer!!', 'Customer Success');
    }, err => {
      this.commonService.showSnackBar('Something went wrong!!', 'Customer Update');
    });
  }
}
